body{
    font-family: "Nunito", sans-serif;
}

.link{
    color: black;
}
.margin_bottom{
    margin-bottom: 20px;
}

.navbar{
    background-color: #FF7512;
    display: flex;
    position: fixed;
    top: 0px;
    width: 100%;
    height: 60px;
    align-items: center;
}

.menu{
    display: flex;
}
.main_logo{
    font-size: 20px;
    color: white;
    font-weight: bold;
   
    text-align: right;
    flex: 8;
}
.menu{
    flex: 50;
    font-size: 14px;
    color: white;
    justify-content: center;
    
