.dashboard{
    margin-top: 60px;
    width: 100vw;
    display: flex;
}

.rating-container {
    width: 200px;
    font-family: Arial, sans-serif;
}
.rating-header {
    display: flex;
    justify-content: space-between;
    font-size: 12px;
    margin-bottom: 5px;
    color: #333;
}
.rating-bar {
    width: 100%;
    height: 20px;
    background-color: #e0e0e0;
    position: relative;
    border-radius: 5px;
    overflow: hidden;
}
.filled-bar {
    height: 100%;
    background-color: #4caf50;
    width: 50%; /* 5 out of 10 is 50% */
    position: absolute;
    top: 0;
    left: 0;
}
.margin_top{
    margin-top: 20px;
    padding-top: 20px;
}
.side-dashboard{
    padding: 20px;
    width: 220px;
    height: 100%;
    flex: 1.5;
}

.heading{
    font-size: 16px;
}

.inline-heading{
    font-size: 14px;
    font-weight: 600;
}
.instances{
    display: flex;
}

.all{
    border: 1px solid #A2A2A2;
    height: 150px;
    width: 130px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.all-instances{
