.show_instances {
    display: flex;
    /* justify-content: center; */
    flex-wrap: wrap; /* Ensures the items wrap to the next line when the screen is smaller */
}

.show_instances ul {
    display: flex;
    flex-wrap: wrap; /* Allows the list items to wrap onto the next line */
    padding: 0px;
    list-style: none;
    /* justify-content: center; */
    margin: 0; /* Ensures no extra margin around the ul */
}

.show_instances li {
    font-size: 12px;
    background-color: white;
    width: 250px; /* Set a fixed width for each item */
    display: flex;
    justify-content: center;
    height: 40px;
    border-radius: 10px;
    margin: 10px; /* Add some space between the items */
    flex-shrink: 0; /* Prevents the items from shrinking */
}

.content {
    display: flex;
    justify-content: space-between;
    width: 85%;
    align-items: center;
}

.all-creds{
    /* background-color: #4caf50; */
    width: 100%;
    margin: 20px 50px;
}

.btn-width{
