}


.credential-section {
    display: flex;
    align-items: center;

    margin-bottom: 10px;
    gap: 10px; /* Adds 10px spacing between each item in the flex container */
}

.credential-section {
    display: flex;
    align-items: center;
    width: fit-content; /* Make the section take up 100% of the width */
    margin-bottom: 10px;
    gap: 10px; /* Adds 10px spacing between each item */
    box-sizing: border-box; /* Ensures padding is included in the element's width */
}

.cred_inp{
    width: 300px;
}

.cred_inp__ {
    white-space: nowrap;       /* Prevent text from wrapping */
    overflow: hidden;          /* Hide the overflowed text */
    text-overflow: ellipsis;   /* Add ellipsis (...) when overflow occurs */
    max-width: 200px;  
    margin-right: 50px;        /* Set the maximum width to 350px */
}

.loading-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh; /* Full height of the viewport */
    position: fixed; /* Fixed position to stay in view */
    width: 100%; /* Full width */
    background: rgba(255, 255, 255, 0.8); /* Slightly transparent background */
    z-index: 1000; /* Ensure it appears above other elements */
  }
  
  .loading-spinner {
    border: 8px solid #f3f3f3; /* Light grey */
    border-top: 8px solid #3498db; /* Blue */
    border-radius: 50%;
    width: 50px; /* Size of the spinner */
    height: 50px; /* Size of the spinner */
    animation: spin 1s linear infinite; /* Animation effect */
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }
.credential-info {
    display: flex;
    /* flex: 0 0 300px; Fixed width of 300px for the credential information */
    overflow: hidden;
    white-space: nowrap; /* Keep text on a single line */
    text-overflow: ellipsis; /* Truncate overflowing text with an ellipsis */
}

.credential-info span {
    margin-right: 10px; /* Add spacing between text items */
}

.credential-actions {
    display: flex;
    gap: 10px; /* Add 10px spacing between buttons */
}
.margin-right__{
    margin-right: 10px;
}
.credential-actions button {
    width: 50px; /* Width for each button */
    padding: 5px; /* Add padding inside the buttons */
    text-align: center;
}

input {
    width: 300px; /* Set width of input fields to 300px */
    padding: 5px; /* Add padding inside the input */
    margin-right: 10px; /* Add 10px spacing between input fields */
    box-sizing: border-box; /* Ensures padding is included in the element's width */
}








