body{
    font-family: "Nunito", sans-serif;
}

.section{
    width: 300px;
    position: absolute;
    left: 10%;
    top: 20%;
}
.nexo{
    color: #FF7512;
}
.vate{
    color: #0075FF;
}
.logo{
    font-size: 30px;
    font-weight: bold;
}
.margin_bottom{
    margin-bottom: 20px;
}
.link{
    color: black;
}

.sub-title{
    font-size: 14px;
}
.primary-input{
border: 1px solid #A2A2A2;
display: flex;
    justify-content: center;
    height: 40px;
}

.primary-input input{
    font-family: "Nunito", sans-serif;
    font-size: 14px;
    border: none;
    outline: none;
    
    width: 270px;
    }

.primary-btn button{
    font-family: "Nunito", sans-serif;
    width: 100%;
    border: none;
    height: 40px;
    background-color: #FF7512;
    color: white;
    font-size: 14px;
}